import axios from '@/utils/axios'

export const createDoc = (data) => axios({
  url: '/admin/document/create',
  data,
  method: 'post'
})

export const createChapter = (data) => axios({
  url: '/admin/chapter/create',
  data,
  method: 'post'
})

